"use client"
import Link from 'next/link'
import React from 'react'
import './Header.scss'
import CartImage from "next/image"
import { wixClientServer } from '@/lib/wixClientServer'

const Header = () => {
    const wixClient = wixClientServer()
    console.log(wixClient)
    const login = async () =>{
        const loginRequestData = wixClient.auth.generateOAuthData(
            "http://localhost:3000",
        );        
       
        localStorage.setItem("oAuthRedirectData", JSON.stringify(loginRequestData))
        const {authURL} = await wixClient.auth.getAuthUrl(loginRequestData)
        window.location.href = loginRequestData.authUrl;
    }


    return (
    <header className='header'>
        <div className='container'>
            <div className='inner_'>
                <div className='logo'>
                    <h1><Link href={'/'}>LOGO</Link></h1>
                </div>
                <div className='navigation'>
                    <ul>
                        <li><Link href={'/'}>Home</Link></li>
                        {/* <li><Link href={'/category/electronics'}>Home</Link></li> */}
                        {/* <li><Link href={{}}>About</Link></li>
                        <li><Link href={{}}>Boys</Link></li>
                        <li><Link href={{}}>Girls</Link></li> */}
                    </ul>
                </div>
                <div className='right_sec'>
                <div href={"/profile"} className='profile' onClick={login}>Login <img src="./user.png" /> </div>
                <Link href={"/cart"} className='cart'> <img src="./shopping-cart.png" /> <span className='count'>2</span></Link>
                </div>
            </div>
        </div>
    </header> 
  )
}

export default Header